.list{
color: #fff;
width:300px;
background-color: darkred;
height: 50px;
margin: 20px auto;
border-radius: 15px;
}
.list p{
    padding: 10px;
    font-size: 20px;
    text-align:left;
  position: relative;
}
.list p span{
    position: absolute;
    right: 10px;
}
.list p input{
    background-color: transparent;
    border:0;
    color: #fff;
    font-size: 18px;
}
.list p input:focus{
    outline: none;
}
.faicons{
    font-size: 15px;
}



