body{
  background-color: peachpuff;
}
.App{
  width: 30%;
  min-height: 500px;
  background:black;
  margin: 40px auto;
  border-radius: 20px;
}
.to-do-list{
  padding: 40px;
}
.to-do-list input{
  margin: 10px 30px;
  padding: 15px;
  height: 20px;
  width: 50%;
  border-radius: 25px;
}
.to-do-list input:focus{
  border: 2px solid cyan;
}
.to-do-list ::placeholder{
font-size: 20px;
text-align: left;
}

.to-do-list button{
  padding: 10px;
  border-radius: 10px;
  font-size: 20px;
  width:20%;
  text-transform: uppercase;
 background-color: orange;
 color: white;
 border: none;
}

.to-do-list button:hover{

  box-shadow: 0 0 5px orange,
              0 0 10px orange,
              0 0 25px orange,
              0 0 50px orange;
}